.jumbotron-aboutme-container {
    padding-top: 129px;
    display: flex;
    flex-direction: row;
    padding-left: 5px;
}
.left{
    margin-left: 85px;
}

.right{
    margin-left:72px;
}

.my-photo {
    width: 418px;
}

.my-name {
    font-family: Montserrat;
    font-style: normal;
    font-weight: bold;
    font-size: 72px;
    line-height: 88px;
}

.my-name-caption {
    font-family: Montserrat;
    font-style: normal;
    font-weight: normal;
    font-size: 24px;
    line-height: 29px;
    margin-left: 13px;
    padding-top:15px;
}

.dot{
    font-family: Montserrat;
    font-style: normal;
    font-weight: bold;
    font-size: 24px;
    line-height: 29px;

    padding: 15px 0;
    margin-left: 13px;
}

.story{
    width: 678px;
}

.story p{
    font-family: Montserrat;
    font-style: normal;
    font-weight: 500;
    font-size: 20px;
    line-height: 26px;
    margin-left: 13px;
}


.p-space{
    padding:15px 0;
}

@media screen and (max-width:768px) {
    .jumbotron-aboutme-container{
        flex-direction: column-reverse;
        padding-top: 20px;
    }
    .left{
        margin:auto;
    }
    .right{
        margin: auto;
    }
    .my-name{
        text-align: center;
        font-size: 30px;
        margin: 0px ;
    }
    .my-name-caption{
        text-align: center;
        font-size: 18px;
        margin: -20px 20px 30px 20px;
        padding: 0;
    }
    .my-image{
        width: 200px;
    }
    .story{
        width: auto;
        text-align: center;
    }
    .dot{
        display: none;
    }
    .story p {
        font-size: 18px;
        margin:0 50px;
    }
    .story p:nth-child(2)
    {
        margin: 15px 50px;
    }
}